




















.navbar nav {
    padding: 0 20px;
}

.index {
  display: grid; /* cards sit next to each other */
  grid-template-columns: 1fr 1fr 1fr; /*3 columns; each column will take 1fr/33% */
  grid-gap: 30px;
  margin-top: 60px;
}
.index h2 {
  font-size: 1.8em;
  text-align: center;
  margin-top: 0;
}
.index .ingredients {
  margin: 30px auto;
}
.index .ingredients li{
  display: inline-block; /* ing sit next to each other */
}
.index .delete{
  position: absolute;
  top: 4px;
  right: 4px;
  cursor: pointer;
  color: #aaa;
  font-size: 1.4em;
}

.add-smoothie {
    margin-top: 60px;
    padding: 20px;
    max-width: 500px;
}
.add-smoothie h2 {
    font-size: 2em;
    margin: 20px auto;
}
.add-smoothie .field {
    margin: 20px auto;
    position: relative;
}
.add-smoothie .delete {
    position: absolute;
    right: 0;
    bottom: 16px;
    color: #aaa;
    font-size: 1.4em;
    cursor: pointer;
}

